import {mapState, mapGetters} from 'vuex';
import {uniCard, uniIcons} from '@dcloudio/uni-ui';
import WucTab from '@/components/wuc-tab/wuc-tab.vue';
import collapse from '../../components/collapse';
import download from '@/components/download';

export default {
  props: {},
  components: {
    uniCard,
    uniIcons,
    collapse,
    WucTab,
    download,
  },
  computed: {
    ...mapState('detail', {}),
    ...mapGetters('detail', ['doc']),
    cases() {
      if (!this.doc) {
        return [];
      }
      let cases = this.doc.archive.cases.offline;
      return cases;
    },
    modules() {
      if (!this.doc) {
        return null;
      }
      let cases = this.cases;
      let modules = [];
      for (let key in cases) {
        modules.push({name: key});
      }
      return modules.length ? modules : null;
    },
    module() {
      if (!this.doc || !this.modules) {
        return null;
      }
      let module = this.modules[this.moduleIdx].name;
      return module;
    },
    categorys() {
      if (!this.doc || !this.module) {
        return null;
      }
      let cases = this.cases[this.module];
      let categorys = Object.keys(cases);
      return categorys;
    },
  },
  watch: {},
  filters: {
    formatName(item) {
      return `${item.name}-${item.alias}`;
    },
  },
  data() {
    return {
      moduleIdx: 0,
      items: [],
    };
  },
  methods: {
    getColor(item) {
      let status = item.status1.doName;
      let color = uni.$color.carrot;
      switch (status) {
        case 'ongoing':
          color = uni.$color.orange;
          break;
        case 'owner_confirm':
          color = uni.$color.carrot;
          break;
        case 'passed':
          color = uni.$color.nephritis;
          break;
        case 'failed':
          color = uni.$color.pomegranate;
          break;
      }
      return color;
    },
    onStatusChange(e) {
      this.$store.commit('detail/setOfflineStatus', e.target.value);
    },
    onModuleChange(idx) {},
    onLogDownload(item) {
      let url = `https://nats-sh.unisoc.com/nginx/download/logs/test/${this.doc.app}_${this.doc._id}/offline${item.log.httpUri}`;
      this.$refs.downloadLog.start(url);
    },
  },
};
